/***/ (function(module, exports) {

	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Lola","date":"29 Jul 2000","slug":"/2000-07-29-lola/","tags":"Y2000"},"html":"<p>In our continuing attempt to catch up with the culture of the 20th century, we watched \"Lola Rennt\" (Run Lola Run) on DVD last night. It tells the same story 3 times, with various twists so that the endings are completely different, and ends by running the credits backwards.</p>\n<p>Discovered by accident: <a href=\"http://WWW.GermanyByNET.com\">GermanyByNET.com</a>, a cheesy German-American portal. However, they do have links to <a href=\"http://www.GermanyByNET.com/newsbot.asp?mode=AFP\">German news stories in English</a>, and are sponsored by <a href=\"http://www.GermanDeli.com\">GermanDeli.com</a>, where Americans can order German grocery products (read: coffee and chocolate).</p>\n<p>It's been 5 days since the Concorde crash, but it continues to dominate the headlines here, even in our <a href=\"http://www.kreiszeitung-wochenblatt.de\">little weekly shopper</a>. There I learned today that one of the victims lived just 5 km away in Buchholz.</p>\n<p>Watch out for the <a href=\"http://netdyslexia.editthispage.com/\">NetDyslexics</a> this weekend! They're getting militant about <a href=\"http://netdyslexia.editthispage.com/swoc\">Sw/oC (Sundays without computers)</a>, and are punishing everyone who visits their site with a <a href=\"http://static.userland.com/images/netdyslexia/alien.gif\">horrible blinking background graphic</a>. But something has gone wrong! The background's already been switched on, and it's still Saturday in most of the world! Including Jena! The horror, the horror...</p>\n<p><a href=\"http://Mama.editthispage.com\">Mama</a>, in case you haven't noticed, is taking a hopefully brief hiatus from her weblog. Her excuse is that, since she has stopped nursing Christopher, the title <a href=\"http://Mama.editthispage.com\">Nursing 'n Surfing</a> is no longer appropriate.</p>"},"next":{"fields":{"title":"Film at 11","date":"27 Jul 2000","slug":"/2000-07-27-film-at-11/"}},"previous":{"fields":{"title":"Without Glasses","date":"31 Jul 2000","slug":"/2000-07-31-without-glasses/"}}},{"node":{"fields":{"title":"Film at 11","date":"27 Jul 2000","slug":"/2000-07-27-film-at-11/","tags":"Y2000"},"html":"<p>A TV film crew is in Lüllau, filming at the farm place across the street. Mama says that Heinz Reincke (Germans will know who he is... he looks like the typical North German grandpa, even though he actually lives in Vienna) had to drive 6 times down our street for the camera. The scene being filmed this evening seemed to involve a big white billy goat.</p>\n<p>When our office was on the Hamburg Harbor, I'd see film crews there practicall every week. Our new office, though, is on the wrong part of the Reeperbahn to attract any film crews. The red light district is at the other end.</p>"},"next":{"fields":{"title":"Home Alone","date":"26 Jul 2000","slug":"/2000-07-26-home-alone/"}},"previous":{"fields":{"title":"Lola","date":"29 Jul 2000","slug":"/2000-07-29-lola/"}}},{"node":{"fields":{"title":"Home Alone","date":"26 Jul 2000","slug":"/2000-07-26-home-alone/","tags":"Y2000"},"html":"<p>Christopher and I are taking the day off together. Mama is a a one-day road trip, and we have to hold down the fort. </p>\n<p>While Christopher took his morning nap. I played with some layout ideas in the <a href=\"http://shanson.weblogs.com/\">Tech Diary</a>. The calendar is too wide for the navigation column in Windows, so I'm trying out a separate \"window\" to the side, with the favorites update underneath. I love the favorites macro, but I don't like the fixed layout and even less that it forces the entire page to wait until it's loaded. </p>\n<p>The <a href=\"http://netdyslexia.editthispage.com\">NetDyslexics</a> have shunted the calendar and their favorites to the bottom of the page, leaving only an anchor link at the top. That's a good idea too.</p>\n<p>We visited my old work colleagues at <a href=\"http://www.int-res.com\">Inter-Research</a>. They still recognized me!</p>"},"next":{"fields":{"title":"Looking Up","date":"25 Jul 2000","slug":"/2000-07-25-looking-up/"}},"previous":{"fields":{"title":"Film at 11","date":"27 Jul 2000","slug":"/2000-07-27-film-at-11/"}}}],"pathPrefix":"","first":false,"last":false,"index":1050,"pageCount":1101}}

/***/ })

